import React from 'react';
import { StyleSheet, Button, TouchableOpacity, TouchableWithoutFeedback, View, Link, Text, Linking } from 'react-native';
import { Audio } from 'expo-av';
import VolumeContext from './VolumeContext'
import DeviceContext from './DeviceContext'

const SoundButton = (props) => {

  const [volume] = React.useContext(VolumeContext)
  const deviceType = React.useContext(DeviceContext)

  async function playSound(cardID, sound) {
    const soundObject = await Audio.Sound.createAsync(
      { uri: 'https://hearthstonesounds.s3.amazonaws.com/'+cardID+'_'+sound+'.wav' },
      {
        shouldPlay: true,
        volume: volume * 0.01
      }
    );
    try {
      await soundObject.playAsync()
      // Your sound is playing!
      } catch (error) {
      // An error occurred!
      }
  }

  const styles = StyleSheet.create({
    button: {
      alignItems: "center",
      textAlign: "center",
      padding: 10,
      width: deviceType == 'mobile' ? "64px" : "72px",
      height: deviceType == 'mobile' ? "40px" : "40px",
      justifyContent: 'center',
      backgroundColor: "#2196F3",
      shadowColor: "#000",
      shadowOffset: {
        width: 0,
        height: 2
      },
      shadowOpacity: 0.25,
      shadowRadius: 3.84,
      elevation: 5
    },
    buttonList: {
      width: deviceType == 'mobile' ? "40px" : "72px",
      height: deviceType == 'mobile' ? "32px" : "40px",
    },
    text: {
      alignItems: "center",
      justifyContent: 'center',
      color: "white",
      fontSize: deviceType == 'mobile' ? 10.5 : 12,
    }
  })

  return (
    <>
      <View style={{padding: 4}}>
      <TouchableOpacity
        style={props.listButton ? [styles.button, styles.buttonList] : styles.button}
        onPress= {() => {
          playSound(props.cardID, props.sound)
        }}
        href={'https://hearthstonesounds.s3.amazonaws.com/'+props.cardID+'_'+props.sound+'.wav'}
        accessibilityRole="link" target=""
      >
        <Text style={styles.text}>{props.title}</Text>
      </TouchableOpacity>
      </View>
    </>
  )
}

export default SoundButton;
