function altSoundsID(cardID) {

  if (['BAR_024','BAR_031','BAR_034t3','BAR_034t5','BAR_533t','BAR_538','BAR_745','CS3_037','SW_072',
        'SW_075','SW_031t8','SW_097','SW_429t','SW_443t','SW_463t','DED_517t','DED_514','AV_113t3t2','AV_215',
        'AV_218t','AV_294','AV_337', 'ONY_001t', 'ONY_012t', 'SCH_199t27','SCH_199t28','SCH_199t29','SCH_199t30',
        'TSC_029t2','TSC_219t2','TSC_219t3','TSC_219t4','TSC_610','TSC_638t3','TSC_638t4','TSC_659','TSC_919','TSC_919t','TSC_932t','TSC_937t3'
      ].includes(cardID) || cardVersionGroup(cardID) != undefined) 
  {
    if (cardID == 'BAR_024') return 'ULD_190'
    if (cardID == 'BAR_031') return 'UNG_076t1'
    if (cardID == 'BAR_034t3') return 'NEW1_017'
    if (cardID == 'BAR_034t4') return 'OG_064a'
    if (cardID == 'BAR_034t5') return 'CFM_335'
    if (cardID == 'BAR_533t') return 'TRL_546'
    if (cardID == 'BAR_538') return 'TRL_309t'
    if (cardID == 'BAR_745') return 'ULD_154t'
    if (cardID == 'CS3_037') return 'UNG_801'
    if (cardID == 'SW_072') return 'UNG_086'
    if (cardID == 'SW_075') return 'OG_326'
    if (cardID == 'SW_031t8') return 'LOE_016'
    if (cardID == 'SW_097') return 'EX1_556'
    if (cardID == 'SW_429t') return 'TRL_546'
    if (cardID == 'SW_443t') return 'CFM_343'
    if (cardID == 'SW_463t') return 'KAR_030'
    if (cardID == 'DED_517t') return 'EX1_089'
    if (cardID == 'DED_514') return 'GIL_838'
    if (cardID == 'AV_113t3t2') return 'EX1_554t'
    if (cardID == 'AV_215') return 'DRG_065'
    if (cardID == 'AV_218t') return 'CS2_tk1'
    if (cardID == 'AV_294') return 'BGS_021'
    if (cardID == 'AV_337') return 'TRL_550'
    if (cardID == 'ONY_001t') return 'GIL_190t'
    if (cardID == 'ONY_012t') return 'TRLA_127'
    if (['SCH_199t27','SCH_199t28','SCH_199t29','SCH_199t30'].includes(cardID)) return 'SCH_199'
    if (cardID == 'TSC_029t2') return 'TSC_029t'
    if (['TSC_219t2','TSC_219t3','TSC_219t4'].includes(cardID)) return 'TSC_219t'
    if (cardID == 'TSC_610') return 'SW_434'
    if (cardID == 'TSC_638t3') return 'TSC_638t2'
    if (cardID == 'TSC_638t4') return 'TSC_638'
    if (cardID == 'TSC_659') return 'BT_133'
    if (['TSC_919','TSC_919t'].includes(cardID)) return 'EX1_089'
    if (cardID == 'TSC_932t') return 'ULD_292'
    if (cardID == 'TSC_937t3') return 'TSC_937t'


    // Cards with version groups will always be mapped to the wild (oldest) version's sounds.
    else if (cardVersionGroup(cardID) != undefined) return cardVersionGroup(cardID).wild
    else return cardID

  } else return cardID

}

