import React from 'react';
import { Text, View, TouchableOpacity } from 'react-native';
import SmallClassImage from './SmallClassImage';
import SoundButton from './SoundButton';
import TribeImage from './TribeImage';
import TechLevelImage from './TechLevelImage';
import {battlegroundTribe, battlegroundTechLevel, altSoundsID} from './HelperFunctions'


const CardListItem = (props) => {

  const item = props.item
  const typeFilter = props.typeFilter
  const showCard = props.showCard

  return (
    <TouchableOpacity onPress= {() => {showCard(item)}}>
      <View style={{ flex: 1, flexDirection: 'row', alignItems: 'center'
      , justifyContent: 'space-between'
      , marginTop: 4, marginBottom: 4, marginRight: 8}}>
        { typeFilter == 'Battlegrounds' ?
            <>
              <TribeImage tribe={battlegroundTribe(item.id)}/>
              <TechLevelImage techlevel={battlegroundTechLevel(item)}/>
            </>
            : <SmallClassImage cardClass={item.cardClass} multiClassGroup={item.multiClassGroup}/>
        }

        <Text style={{flexGrow: 1}} >{item.name}</Text>
        {item.type == 'MINION' && !['BGS_046t', 'BAR_919t'].includes(item.id) ?
          <>
          {['KAR_114','DAL_058','LOOT_013','AT_114','DAL_085','DMF_121','SW_073'].includes(item.id) ? <></> :
            <>
              <SoundButton title='Play' cardID={altSoundsID(item.id)} sound='P' listButton={true} />
            </>
          }
          {(['DAL_058','LOOT_013'].includes(item.id)) ? <></> :
            <>
              <SoundButton title='Attack' cardID={altSoundsID(item.id)} sound='A' listButton={true} />
            </>
          }
          <SoundButton title='Death' cardID={altSoundsID(item.id)} sound='D' listButton={true} />
          </>
          : <></>
      }
      </View>
    </TouchableOpacity>
  )
}

export default CardListItem
